___CSS_LOADER_EXPORT___.push([module.id, `body {
  box-sizing: border-box;
  margin: 0;

  font-family: Arial, Helvetica, sans-serif;
  min-height: 100vh;
  padding: max(10px, 5%);
  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.4),
      rgba(0, 0, 0, 0.4)
    ),
    url(https://images.pexels.com/photos/912110/pexels-photo-912110.jpeg);
  background-size: cover;
  background-position: center;
}

#weather-app {
  color: white;
}

#weather-app #location-form #searchbar,
#weather-app #location-form button {
  border: none;
  border-radius: 4px;
  padding: 4px;
}

#weather-app #location-form #searchbar {
  margin-right: 4px;
}

#weather-app #location-form button:hover {
  cursor: pointer;
}

#weather-app #weather-condition-container {
  display: grid;
  grid-template-columns: auto auto 1fr;
  grid-template-rows: repeat(2, 1fr);
  align-items: center;
  gap: 4px;
}

#weather-app #weather-condition-container * {
  margin: 0;
}

#weather-condition-container #weather-icon {
  grid-row: 1 / 3;
}

#weather-condition-container #temp {
  grid-row: 1 / 3;
  font-size: 2.5rem;
  margin-right: 15px;
}

#weather-condition-container #weather-condition {
  align-self: end;
  font-weight: bold;
  font-size: 1.1rem;
}

#weather-condition-container #feelslike {
  align-self: start;
  font-size: 0.9rem;
}

#weather-data #sub-data {
  display: flex;
  gap: 16px;
}

#weather-data #sub-data .data-container {
  flex: 1;
}

#weather-data #sub-data .data-container .data-label {
  font-weight: bold;
}
